.gpt3__navabar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
}
.gpt3__navabar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.gpt3__navabar-links_logo {
    margin-right: 2rem;
}
.gpt3__navabar-links_logo img {
    width: 62.56px;
    height: 16px;
}
.gpt3__navabar-links_container {
    display: flex;
    flex-direction: row;
}
.gpt3__navabar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.gpt3__navabar-links_container p,
.gpt3__navabar-sign p,
.gpt3__navabar-menu_container p {
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.gpt3__navabar-sign button,
.gpt3__navabar-menu_container button {
    padding: 0.5rem 1rem;
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    left: 1153px;
    top: 55px;
    border: 0;
    outline: none;
    cursor: pointer;
    background-color: #ff4820;
    border-radius: 5px;
}
.gpt3__navabar-menu {
    margin-left: 1rem;
    display: none;
    position: relative;
}
.gpt3__navabar-menu svg {
    cursor: pointer;
}
.gpt3__navabar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    text-align: end;
    background-color: var(--color-footer);
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}
.gpt3__navabar-menu_container p {
    margin: 1rem 0;
}
.gpt3__navabar-menu_container-links-sign {
    display: none;
}
@media (max-width: 1050px) {
    .gpt3__navabar-links_container {
        display: none;
    }
    .gpt3__navabar-menu {
        display: flex;
    }
}
@media (max-width: 700px) {
    .gpt3__navabar {
        padding: 2rem 4rem;
    }
}
@media (max-width: 500px) {
    .gpt3__navabar {
        padding: 2rem;
    }
    .gpt3__navabar-sign {
        display: none;
    }
    .gpt3__navabar-menu_container {
        top: 20px;
    }
    .gpt3__navabar-menu_container-links-sign {
        display: block;
    }
}